section.tipoUsuario {
   width: 100%;
   height: calc(100vh - 4em);
   display: flex;
   align-items: center;
   justify-content: center;
}

.tipoUsuario .opciones {
   width: 100%;
   height: 50%;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   flex-direction: row;
}