.button {
  padding: 0.5em 2.5em;
  border-radius: 5em;
  cursor: pointer;
  border: none;
  transition: all 0.3s ease;

  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.button:hover {
  filter: brightness(1.1);
  transition: all 0.3s ease;
}

.button img{
  width: 1rem;
  height: 1rem;
}