.t {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 0.5em;
}

.toggle {
   content: '';
   border-radius: 5em;
   height: 1em auto;
   width: 2.5em;
   padding: 0.2em;
   background-color: #282c34;

   display: flex;
   align-items: center;
}

.Light .toggle {
   background-color: #282c34;
   justify-content: flex-start;
   box-shadow: 0 0 15px 1px #282c3480;
   transition: all 0.3s ease;
}

.Dark .toggle {
   background-color: aliceblue;
   box-shadow: 0 0 15px 1px #f0f8ff80;
   justify-content: flex-end;
   transition: all 0.3s ease;
}

.toggle .circulo {
   content: '';
   width: 1.2em;
   height: 1.2em;
   border-radius: 5em;
   cursor: pointer;
   z-index: 10;
}

.circulo:hover {
   transform: scale(0.95);
}

.Light .toggle .circulo {
   background-color: aliceblue;
}

.Dark .toggle .circulo {
   background-color: #282c34;
}